@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    li {
        @apply p-4 cursor-pointer text-gray-800
        hover:text-black hover:scale-105
    }

    button {
        @apply text-white border bg-indigo-600 border-indigo-600
        hover:bg-transparent hover:text-indigo-600 rounded-md
    }
}